:root {
  --font-body: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --red: hsl(0, 100%, 74%);
  --green: hsl(154, 59%, 51%);
  --blue: hsl(248, 32%, 49%);
  --darkblue: hsl(249, 10%, 26%);
  --grayishblue: hsl(246, 25%, 77%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  background-image: url(../app/images/bg-intro-desktop.png);
  background-position: center;
  background-repeat: repeat-y;
  background-size: cover;
  background-color: var(--red);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  display: grid;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  max-width: 90rem;
  margin: auto;
}

.attribution p {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

.intro {
  display: grid;
  grid-template-columns: repeat(2, 542px);
  gap: 0.1rem;
}
@media (max-width: 75em) {
  .intro {
    grid-template-columns: 542px;
  }
}
@media (max-width: 36.875em) {
  .intro {
    grid-template-columns: 340px;
  }
}
.intro-headline {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: var(--white);
  margin-left: 4rem;
}
@media (max-width: 36.875em) {
  .intro-headline {
    margin: 0;
    text-align: center;
  }
}
.intro-headline h1 {
  font-weight: 700;
  font-size: 3rem;
  margin-top: 4rem;
}
@media (max-width: 36.875em) {
  .intro-headline h1 {
    font-size: 1.8rem;
    line-height: 1.2;
  }
}
.intro-headline h6 {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.8;
  margin-top: 4rem;
}
@media (max-width: 75em) {
  .intro-headline h6 {
    margin-top: 2rem;
  }
}
@media (max-width: 36.875em) {
  .intro-headline h6 {
    margin-top: 2rem;
  }
}
.intro-form .free-trial {
  background-color: var(--blue);
  color: var(--white);
  text-align: center;
  padding: 1rem;
  border-radius: 10px;
  margin: 1rem auto;
  border: none;
  box-shadow: 0 8px 1px rgba(0, 0, 0, 0.144);
}
@media (max-width: 36.875em) {
  .intro-form .free-trial {
    padding: 1rem 4rem;
  }
}
.intro-form form {
  display: flex;
  flex-direction: column;
  padding: 2rem 1rem;
  background-color: var(--white);
  height: 500px;
  border-radius: 10px;
  box-shadow: 0 8px 1px rgba(0, 0, 0, 0.144);
}
.intro-form form input {
  display: block;
  width: 90%;
  padding: 1rem 2rem;
  margin: 1rem auto;
  border-radius: 5px;
  border: 1px solid var(--grayishblue);
  font-family: var(--font-body);
  font-weight: 600;
  font-size: 0.9rem;
}
@media (max-width: 36.875em) {
  .intro-form form input {
    padding: 1rem;
  }
}
.intro-form form input:focus {
  outline: 1px solid var(--darkblue);
  border: none;
}
.intro-form form input.error {
  border: 2px solid var(--red);
  color: var(--red);
  outline: none;
}
.intro-form form label {
  position: absolute;
  margin: 0;
  padding: 0;
  font-style: italic;
  color: var(--red);
  font-size: 0.7rem;
  top: 74px;
  left: 322px;
}
@media (max-width: 36.875em) {
  .intro-form form label {
    left: 130px;
  }
}
.intro-form form img {
  position: absolute;
  top: 35px;
  left: 440px;
}
@media (max-width: 36.875em) {
  .intro-form form img {
    left: 255px;
  }
}
.intro-form .fn {
  position: relative;
}
.intro-form .ln {
  position: relative;
}
.intro-form .em {
  position: relative;
}
.intro-form .pw {
  position: relative;
}
.intro-form button {
  display: block;
  width: 90%;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 500;
  background-color: var(--green);
  color: var(--white);
  text-align: center;
  padding: 1rem;
  border-radius: 5px;
  margin: 1rem auto;
  border: none;
  box-shadow: 0 5px 5px var(--green);
  cursor: pointer;
}
.intro-form button:hover {
  opacity: 0.7;
}
.intro-form p {
  margin: 0;
  text-align: center;
  color: var(--grayishblue);
  font-size: 0.7rem;
}
.intro-form p span > a {
  color: var(--red);
}
@media (max-width: 36.875em) {
  .intro-form p {
    padding: 0 2rem;
  }
}